@import "root.css";
.about {
    color: #fff;
}

/* Top Section*/
/* Overall */
.top {
    
    display: flex;
    align-items: center;
    justify-content:center;
}
/* Media query */
@media only screen and (max-width : 1028px) {
    .top
    {   display: flex;
        
        flex-direction: column-reverse;
    }
}
/* Image*/
.top img
{
    width: 60%;
    height: fit-content;
    margin: 10px;
}

.intop{
    height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    /* margin-left: 8%; */
    gap: 3%;
}
/* Media query */
@media only screen and (max-width : 1028px) {
    .intop{
        height: 200px;
    }
}
/* Root section within top */
.root{
    margin-left: 8%;
}
.root span {
    color: #FA8305;
}
/* For Text container inside top*/
.intop0 {
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 65%;
}
/* Heading */
.intop h2 {
    font-weight: 700;
    font-size: 30px;
}
/* Media query*/
@media only screen and (max-width : 1200px) {
    .intop h2{
        font-size: 20px;
    }
}
/* Para */
.intop p {
    width: fit-content;
    font-weight: 400;
    font-size: 15px;
}
/* Get in touch */
.intop h3 {
    padding: 10px;
    font-weight: 500;
    width: fit-content;
    font-size: 16px;
    background: #FA8305;
    border-radius: 40px;
    cursor: pointer;
    margin-bottom:0;
}

/* tile section */
/* Overall */
.tile {
    display: flex;
    flex-direction: column;
    justify-content:space-around;
    align-items: flex-start;
    width: 100%;
    height: fit-content;
    margin-top: 10%;
}
/* Span */
.tile span {
    margin-bottom: 5%;
    font-weight: 600;
    font-size: 25px;
    margin-left: 60px;
}
/* Container for tiles */
.intile {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 10px;
    height: fit-content;
}
/* tile */
.tile1 {
    height: 200px;
    width: 350px;
    padding: 40px;
    margin: 40px;
    background: #1C140F;
    border-radius: 20px;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
/* span */
.tile1 span{
    border-radius: 8px;
    font-weight: 400;
    font-size: 14px;
    padding: 10px;
    width: 70%;
}
/* tile heads */
#tilehead1{
    background: rgba(176, 0, 220, 0.11);
}
#tilehead2{
    background: rgba(220, 66, 0, 0.11);
}
#tilehead3{
    background: rgba(0, 220, 141, 0.11);
}
/* Heading */
.tile1 h3{
    font-weight: 600;
font-size: 20px;
}
/* Para */
.tile1 p{
    font-weight: 400;
    font-size: 14px;
}

/* middle section*/
/* Overall */
.middle{
   
    background: #1C140F;
    display: flex;
    justify-content: space-around;
    height: fit-content;
    width: 100%;
}

/* Media Queries */
@media only screen and (max-width : 1299px) {
    .middle{
        margin-top: 45%;
    }
}
@media only screen and (max-width : 875px) {
    .middle{
        margin-top: 80%;
        flex-direction: column;
        align-items: center;
    }
}
/* Image */
.middle img{
    margin-top: 10%;
    width: 40%;
}
.inmid{
    margin-left:5%
}
/* Media Query */
@media only screen and (max-width : 875px) {
    .inmid{
        width: 80%;
    }
}
/* Container for text */
.inmid1{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 10%;
    width: 35%;
    margin-left: -10%;
}
/* Span */
.inmid1 span{
    font-weight: 500;
    font-size: 16px;
}
/* Heading */
.inmid1 h3{
    font-weight: 600;
    font-size: 28px;
}
/* Para */
.inmid1 p{
    font-weight: 400;
font-size: 16px;
}

/* icontile section*/
/* Overall */
.icontile{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: fit-content;
}
/* container for icons*/
.inicon{
    display: flex;
    justify-content: center;
    margin: 5%;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    padding: auto;
    height: fit-content; 
}
/*  icon */
.icon1{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #1C140F;
    border-radius: 20px;
    width: 250px;
    height: fit-content;
    padding: 10px;
    margin: 1.5%;
}