@import "root.css";
@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:ital,wght@0,100;0,300;0,400;0,500;0,600;1,400;1,500&family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,700;0,800;1,200;1,300;1,400;1,600&display=swap');
*{
    font-family: 'Popins';
}
/* Navbar */
/* Nav section */
.nav {
    height: 100px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: url("./assest/joystick.png");
    background-repeat: no-repeat;
    height: 800px;
    background-position-x: right;
    background-size: 45%;
    
}
/* Logo section within nav*/
.logo {
    position: absolute;
    width: 82px;
    height: 45px;
    left: 3.5%;
    top: 37px;
    
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 800;
    font-size: 29px;
    line-height: 153.5%;
    color: #FFFFFF;
}
/* Navigation operations */
.navop {
    width: 50%;
    position: absolute;
    left: 50%;
    display: flex;
    flex-direction: row;
    gap: 17%;
    top: 44px;
}
/* Media query */
@media only screen and (min-width:800px ) and (max-width :1500px)
{
    .navop
    {
        display: flex;
    }
}
/* Media query */
@media only screen and (max-width: 700px) {
    .navop{
        height: fit-content;
        width: 30%;
        display: "none";
        flex-direction: column;
        background: var(--color);
        align-items: center;
        gap: 15px;
        right: 5px;
    }
}
/* Navop1 section within navop*/
.navop1 {
    display: flex;
    width: 50%;
    gap: 15px;
    flex-direction: row;
    padding: 0px;
    justify-content: space-around;
    margin-top: 1.5%;
}
/*  A */
.navop1 a {
    text-decoration: none;
    color: #FFFFFF;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    position: relative;
}
/*  */
.navop1 a:after {
    content: "";
    position: absolute;
    width: 0%;
    height: 3px;
    bottom: 1px;
    background-color:var(--color);
    transition:0.5s ease-out;
    left: 0;
  }
/*  */
.navop1 a:hover:after {
    transform: scaleX(1);
    width: 100%;
  }
/* Media query */
 @media only screen and (max-width: 700px) {
    .navop1{
        flex-direction: column;
        align-items: flex-end;
        
        gap: 10px;
    }
}
/* Navop1 section within navop*/
/* Button */
.navop2 button {

    left: 86.46%;
    right: 4.39%;
    top: 0.82%;
    bottom: 98.41%;

    background:var(--color);
    border-radius: 6px;
    padding: 10px 20px;
    cursor: pointer;

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 20px;

    color: #FFFFFF;
    text-decoration: none;
}
